.social-links {
  width: 100%;
  height: 100%;
}

.social {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 40px 0;
  height: 100%
}

.social .item svg {
  font-size: 70px;
  color: #fff;
  cursor: pointer;
  -webkit-transition: .1s all;
  -o-transition: .1s all;
  transition: .1s all;
}

.social .item svg:hover {
  color: rgba(255,255,255,0.7);
  -webkit-transition: .1s all;
  -o-transition: .1s all;
  transition: .1s all;
}

@media only screen and (max-width: 800px) {
  .social {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .social .item svg {
    font-size: 40px;
  }
}


